
import { defineComponent, inject, onMounted, ref } from "vue";

export default defineComponent({
  name: "ListItem",

  props: {
    data: {
      type: Object,
      required: true,
    },
    ind: {
      type: Number,
      required: true,
    },
  },
  setup(props) {
    const deleteButtonIsShow = ref(false);

    const dialogVisible = ref(false);
    const input = ref("");
    const handleClose = () => {
      dialogVisible.value = false;
    };
    const editTodo = () => {
      dialogVisible.value = true;
      input.value = props.data.content;
    };
    const changeTodoFun = inject("changeTodo") as Function;
    const handleChangeToDo = () => {
      changeTodoFun(props.ind, input.value);
      dialogVisible.value = false;
    };

    const deleteTodoFun = inject("deleteTodo") as Function;
    const deleteTodo = () => {
      deleteTodoFun(props.ind);
    };
    return {
      deleteButtonIsShow,
      deleteTodo,
      editTodo,

      dialogVisible,
      handleClose,
      input,
      handleChangeToDo,
    };
  },
});
